export default {
  props: {
    book: {
      type: Object
    }
  },
  methods: {
    handleShowDialog() {
      this.$store.commit({
        type: "light/setState",
        key: "isListDialogVisible",
        value: true
      });
      const book = this.$props.book;
      book.count = 1;
      this.$store.commit({
        type: "light/setState",
        key: "addBook",
        value: book
      });
    },
    handleImageClick(id) {
      this.$router.push(`/light/detail/${id}`);
    }
  }
};
